import Home from './pages/React页面/Home';
import Doc from './pages/React页面/Doc';
import Tuto from './pages/React页面/Tuto';
import Blog from './pages/React页面/Blog';
import Comm from './pages/React页面/Comm';
import Core from './pages/React页面/Core';
import Guide from './pages/React页面/Guide'
import Api from './pages/React页面/Api';
import Hooks from './pages/React页面/Hooks';
import Login from './pages/React页面/Login';

const routes = [
    {
        path:'/',
        exact:true,
        component:Home,
    },
    {
        path:'/doc',
        component:Doc,
        pri:true,
        routes:[
            {
                path:'/doc/core',
                component:Core

            },
            {
                path:'/doc/guide',
                component:Guide
            },
            {
                path:'/doc/api',
                component:Api
            },
            {
                path:'/doc/hooks',
                component:Hooks
            }
        ]
    },
    {
        path:'/tuto',
        pri:true,
        component:Tuto
        
    },
    {
        path:'/blog',
        component:Blog
    },
    {
        path:'/comm',
        component:Comm
    },
    {
        path:'/login',
        component:Login
    }
        
]

export default routes;