const App = ()=>{
	return <Router>
		<Header />
		<Switch>
			{
				routes.map(({pri,...rest})=>{
					if(pri){
						return <PrivateRoute {...rest} />
					}
					return <RouteWithSubRoutes {...rest} />
					}
				)
			}
		</Switch>
	</Router>
}

export default App;

