import React from 'react'
import {HashRouter as Router,Redirect,Route,NavLink,Switch, withRouter} from 'react-router-dom'

const PrivateRoute = ({ component:Com, ...rest }) => {
    return (
        <Route
          {...rest}
          render={({ location }) =>
             localStorage.getItem('userinfo')
             ?<Com {...location}/> 
             : (
              <Redirect
                to={{
                  pathname: "/login",
                  state: location
                }}
              />
            )
          }
        />
      );
}

export default PrivateRoute
